.footer {
    background: -webkit-linear-gradient(233deg, rgba(162, 0, 255, 0.918), rgba(255,0,0,0) 70.71%),
    -webkit-linear-gradient(323deg, rgba(0, 255, 149, 0.76), rgba(0,255,0,0) 70.71%),
    -webkit-linear-gradient(114deg, rgba(0,0,255,.8), rgba(0,0,255,0) 70.71%);
    background: -o-linear-gradient(233deg, rgba(162, 0, 255, 0.918), rgba(255,0,0,0) 70.71%),
    -o-linear-gradient(323deg, rgba(0, 255, 149, 0.76), rgba(0,255,0,0) 70.71%),
    -o-linear-gradient(114deg, rgba(0,0,255,.8), rgba(0,0,255,0) 70.71%);
    background: linear-gradient(217deg, rgba(162, 0, 255, 0.918), rgba(255,0,0,0) 70.71%),
    linear-gradient(127deg, rgba(0, 255, 149, 0.76), rgba(0,255,0,0) 70.71%),
    linear-gradient(336deg, rgba(0,0,255,.8), rgba(0,0,255,0) 70.71%);   
    
}

.footer:hover {
    background: 
    -webkit-linear-gradient(323deg, rgba(0, 255, 149, 0.76), rgba(0,255,0,0) 70.71%),
    -webkit-linear-gradient(114deg, rgba(0,0,255,.8), rgba(0,0,255,0) 70.71%),
    -webkit-linear-gradient(233deg, rgba(162, 0, 255, 0.918), rgba(255,0,0,0) 70.71%);
    background: 
    -o-linear-gradient(323deg, rgba(0, 255, 149, 0.76), rgba(0,255,0,0) 70.71%),
    -o-linear-gradient(114deg, rgba(0,0,255,.8), rgba(0,0,255,0) 70.71%),
    -o-linear-gradient(233deg, rgba(162, 0, 255, 0.918), rgba(255,0,0,0) 70.71%);
    background: 
    linear-gradient(127deg, rgba(0, 255, 149, 0.76), rgba(0,255,0,0) 70.71%),
    linear-gradient(336deg, rgba(0,0,255,.8), rgba(0,0,255,0) 70.71%),
    linear-gradient(217deg, rgba(162, 0, 255, 0.918), rgba(255,0,0,0) 70.71%); 
        
}

p {
    color: white;
}


